<template>
  <div>
    <div class="top-wrapper">
      <div class="location">
        <div class="content">
          <div class="iconfont icon-locationfill"></div>
          <div class="font">德丰大楼</div>
        </div>
        <div class="button">商</div>
      </div>
      <div class="seach">
        <div class="iconfont icon-search"></div>
        <div class="font">搜索商品</div>
      </div>
    </div>
    <div class="list-wrapper">
      <div class="team">
        <div class="item" v-for="(item, index) in list" v-if="index < 5" :key="index" @click="toGoods">
          <div class="iconfont icon-dan img"></div>
          <div class="font">{{item.name}}</div>
        </div>
      </div>
      <div class="team">
        <div class="item" v-for="(item, index) in list" v-if="index > 4" :key="index" @click="toGoods">
          <div class="iconfont icon-dan img"></div>
          <div class="font">{{item.name}}</div>
        </div>
      </div>
    </div>
    <div class="swiper-wrapper">
      <swiper class="swiper" indicator-dots="true" autoplay="true" interval="5000" duration="1000">
        <block v-for="(item, index) in imgUrls" :index="index" :key="key">
          <swiper-item>
            <image :src="item" class="slide-image" mode="aspectFill" />
          </swiper-item>
        </block>
      </swiper>
    </div>
    <div class="news-wrapper">
      <div class="item" v-for="(item,index) in news" :key="key">
        <img :src="item.img" class="img" />
        <div class="content">
          <div class="title">{{item.title}}</div>
          <!--<div class="desc">{{item.desc}}</div>-->
        </div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  data () {
    return {
      imgUrls: [
        'http://img02.tooopen.com/images/20150928/tooopen_sy_143912755726.jpg',
        'http://img06.tooopen.com/images/20160818/tooopen_sy_175866434296.jpg',
        'http://img06.tooopen.com/images/20160818/tooopen_sy_175833047715.jpg'
      ],
      news: [
        {
          img: '//z6.sinaimg.cn/auto/resize?img=https%3A%2F%2Fn.sinaimg.cn%2Ftranslate%2F746%2Fw960h586%2F20180807%2FWySh-hhkusks8851274.jpg&size=200_134',
          title: '日外相会晤昂山素季 承诺扩大对缅甸基础设施援助',
          desc: '描述'
        },
        {
          img: '//z0.sinaimg.cn/auto/resize?img=https%3A%2F%2Fn.sinaimg.cn%2Fdefault%2Ftransform%2F116%2Fw550h366%2F20180517%2Fq7Vh-harvfhu4569176.png&size=200_134',
          title: '印尼地震细节披露:游客爆料想登救生艇被索要费用',
          desc: '描述'
        },
        {
          img: '//z9.sinaimg.cn/auto/resize?img=https%3A%2F%2Fn.sinaimg.cn%2Fdefault%2Ftransform%2F266%2Fw640h426%2F20180807%2FrSrR-hhkusks9302556.jpg&size=200_134',
          title: '台当局改口以中华台北之名参加奥运 被赞知错能改',
          desc: '描述'
        },
        {
          img: '//z7.sinaimg.cn/auto/resize?img=https%3A%2F%2Fn.sinaimg.cn%2Fsinacn17%2F40%2Fw480h360%2F20180804%2F13e7-hhhczfa4232205.jpg&size=200_134',
          title: '美国称赞“白头盔”英雄，却因这举动露馅，外媒：这下没话说了！',
          desc: '描述'
        },
        {
          img: '//z3.sinaimg.cn/auto/resize?img=https%3A%2F%2Fn.sinaimg.cn%2Fsinacn12%2F766%2Fw1002h564%2F20180806%2F0109-hhhczfc3651492.jpg&size=200_134',
          title: '司机遇查龟速逃跑，交警一路陪跑300米：要不歇会',
          desc: '描述'
        }
      ],
      list: [
        {
          name: '早餐'
        },
        {
          name: '蔬菜'
        },
        {
          name: '禽蛋'
        },
        {
          name: '肉制品'
        },
        {
          name: '水产'
        },
        {
          name: '瓜果'
        },
        {
          name: '面豆制品'
        },
        {
          name: '调味品'
        },
        {
          name: '干货'
        },
        {
          name: '杂货'
        }
      ]
    }
  },

  components: {},

  methods: {
    toGoods () {
      wx.navigateTo({
        // url: '/pages/ewords/main?id=' + id
        url: '/pages/goods/main'
      })
    }
  },

  created () {
  },
  onReachBottom () {
    let item = [
      {
        img: '//z8.sinaimg.cn/auto/resize?img=https%3A%2F%2Fn.sinaimg.cn%2Ffront%2F38%2Fw419h419%2F20180806%2FEGTz-fzrwica0254999.jpg&size=200_134',
        title: '富士康缩减美国威斯康辛项目规模 工厂降级',
        desc: '描述'
      },
      {
        img: '//z7.sinaimg.cn/auto/resize?img=https%3A%2F%2Fn.sinaimg.cn%2Ffront%2F200%2Fw640h360%2F20180806%2FnB5Y-hhhczfc7069804.jpg&size=200_134',
        title: '北大教授批抗日神剧：无视民族苦难 宣扬阿Q精神',
        desc: '描述'
      },
      {
        img: '//z7.sinaimg.cn/auto/resize?img=https%3A%2F%2Fn.sinaimg.cn%2Ftranslate%2F333%2Fw690h443%2F20180807%2FRJGx-hhkusks8325514.jpg&size=200_134',
        title: '这个地方借债10亿 用大学学费住宿费伙食费作抵押',
        desc: '描述'
      },
      {
        img: '//z4.sinaimg.cn/auto/resize?img=https%3A%2F%2Fn.sinaimg.cn%2Fsinacn17%2F750%2Fw480h270%2F20180806%2Fc4f5-hhhczfc6039768.png&size=200_134',
        title: '越南网吧一群小学生打游戏，老板在17秒的监控画面发现亮点',
        desc: '描述'
      },
      {
        img: '//z6.sinaimg.cn/auto/resize?img=https%3A%2F%2Fn.sinaimg.cn%2Ffront%2F58%2Fw549h309%2F20180802%2FK8in-fzrwica0039322.jpg&size=200_134',
        title: '联合国突然下狠手！日本损失惨重 中国这次赢了不少',
        desc: '描述'
      }
    ]
    this.news.push(...item)
    console.log(this.news)
  }
}
</script>

<style scoped>
@import "../../utils/iconfont/iconfont.css";

/* 顶部 */
.top-wrapper {
  padding: 0 3%;
  background: rgb(73, 177, 82);
  height: 92px;
}

.top-wrapper .location {
  display: flex;
  justify-content: space-between;
  color: white;
  padding-top: 2%;
}

.top-wrapper .location .content {
  display: flex;
}

.top-wrapper .location .content .icon-locationfill {
  line-height: 25px;
  font-size: 20px;
}

.top-wrapper .seach {
  display: flex;
  background: white;
  box-sizing: border-box;
  margin: 3% 0;
  border-radius: 20px;
  height: 40px;
  align-items: center;
  justify-content: center;
}

.top-wrapper .seach .font {
  font-size: 14px;
}

.top-wrapper .seach .icon-search {
  font-size: 16px;
}

/* 分类 */

.list-wrapper {
  display: flex;
  flex-direction: column;
}

.list-wrapper .team {
  display: flex;
}

.list-wrapper .team .item {
  display: flex;
  flex-direction: column;
  flex: 1;
  height: 80px;
}

.list-wrapper .team .item .img {
  background: rgb(73, 177, 82);
  color: white;
  display: inline-block;
  height: 40px;
  font-size: 16px;
  line-height: 40px;
  text-align: center;
  border-radius: 50%;
  box-sizing: border-box;
  margin: 10% 22%;
}

.list-wrapper .team .item .font {
  text-align: center;
  font-size: 14px;
}

/* 轮播图 */
.swiper-wrapper {
  margin-bottom: 3%;
}

.swiper-wrapper .swiper {
  height: 140px;
}

.swiper-wrapper .slide-image {
  width: 100%;
}

/* 新闻 */
.news-wrapper .item {
  display: flex;
  padding: 0 3% 3% 3%;
}

.news-wrapper .item .img {
  display: flex;
  background: black;
  height: 75px;
  flex: 0 0 100px;
}

.news-wrapper .item .content {
  display: flex;
  flex-direction: column;
  flex: 1;
  align-items: center;
  justify-content: center;
  margin-left: 10px;
}
</style>



